import React from "react"
import { graphql } from "gatsby"
import { ThemeToggler } from 'gatsby-plugin-dark-mode'
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Projects from "../components/projects"

const BlogIndex = ({ data, location }) => {
  return (
    <Layout location={location} title="{siteTitle}">
      <SEO title="Marissa Phul" keywords={[`blog`, `software engineer`]}/>
      <div className="row">
        <div className="column sub-header">
          <h1 className="title">Hi, I'm Marissa
          <span className="site-accent"><ThemeToggler>
                    {({ theme, toggleTheme }) => (
                        <label style={{ cursor: `pointer`, opacity: 0.8 }}>
                        <input
                            style={{ display: `none` }}
                            type="checkbox"
                            onChange={e => toggleTheme(e.target.checked ? "dark" : "light")}
                            checked={theme === "dark"}
                        />
                        {theme === "dark" ? `‚òÄÔ∏è` : `üåô`}
                        </label>
                    )}
            </ThemeToggler></span>
            {/* <span className="site-accent">.</span>             */}
          </h1>
          <p className="starting-bio">I like breaking down complex problems and building from the ground-up to create human-centered systems and bring people together, on and offline.</p>
          <div className="buttons">
            <Link to="/about">‚ûú about</Link><br></br>
            <Link to="/blog">‚ûú blog</Link><br></br>            
          </div>
        </div>
        <div className="column fixed-column">          
          <Projects data={data}/>
        </div>
      </div>
    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    allMarkdownRemark(limit: 2, sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
          node {
            fields {
              slug
            }
            frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
                description
            }
          }
      }
    }
  }
`
